
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import Tabs from '@/components/tabs/tabs';
export default {
components: {
XHeader,
InlineLoading,
Tabs
},
data() {
return {
panes: [],
panes1: [],
rightArr: [],
list: {},
rightLoading: false,
CBM: '',
conIndex: 0,
liIndex: -1,
gotoDetailIndex: 0,
newConIndex: 0,
firstSet: true, //设置一级top
firstTwoSet: 0, //设置二级获取top 次数
}
},
beforeRouteLeave(to, from, next) {
if (to.name == 'doctorList') {
window.localStorage.setItem('gotoSymptomDetails', JSON.stringify({
index: this.gotoDetailIndex,
conIndex: this.newConIndex
}));
} else {
window.localStorage.removeItem('gotoSymptomDetails');
}
next();
},
mounted() {
if (this.$route.params.listHospitalKS) {
this.CBM = this.$route.params.listHospitalKS;
this.init1();
} else {
this.init();
}
let gotoDetail = JSON.parse(window.localStorage.getItem('gotoSymptomDetails'));
if (gotoDetail) {
this.gotoDetailIndex = gotoDetail.index;
this.newConIndex = gotoDetail.conIndex;
}
},
updated() {
if (this.$refs.tabasss.$refs.myBoxLi && this.firstSet) {
this.firstSet = false;
this.liIndex = 0;
this.liIndex = this.gotoDetailIndex;
this.conIndex = this.newConIndex
}
},
methods: {
init() { // 一级科室  从医生入口
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post(this.$root.host, {
"MSH.1": "doctorregistration",
"MSH.2": "listHospitalKS",
sjksmc: '', // 上级科室名称
pageIndex: 1,
pageSize: 200,
token: this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
if (typeof res.rows != 'undefined') {
console.log(res.rows)
res.rows.forEach(item => {
this.panes.push({
label: item.CKSMC
});
})

let ii = this.gotoDetailIndex;
this.getRight(this.panes[ii].label);
} else {
this.panes = [];
}
}).catch(error => {
console.log(error);
})
},
init1() { // 一级科室  从医院入口
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post(this.$root.host, {
"MSH.1": "hospitalregistration",
"MSH.2": "listHospitalKS",
cbm: this.CBM, // 机构编码
sjksbm: '', // 科室编码,如果是一级科室 传空
token: this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
if (typeof res.rows != 'undefined') {
res.rows.forEach(item => {
this.panes.push({
label: item.CKSBM
});
this.panes1.push({
label: item.CKSMC
});
})

let kk = this.gotoDetailIndex;
this.getRightList(this.panes[kk].label);
} else {
this.panes1 = [];
}
}).catch(error => {
console.log(error);
})
},
getRight(code) { // 医生入口
this.rightArr = [];
this.rightLoading = true;
this.$ajax.post(this.$root.host, {
"MSH.1": "doctorregistration",
"MSH.2": "listHospitalKS",
sjksmc: code, // 上级科室名称
pageIndex: 1,
pageSize: 200,
token: this.$root.loginInfo.token,
}).then(res => {
this.rightLoading = false;
this.rightArr = [];
if (typeof res.rows != 'undefined' && res.rows.length > 0) {
res.rows.forEach(item => {
this.rightArr.push({
label: item.CKSMC
});
})
} else {
this.rightArr = []
}
this.list[code] = this.rightArr;
if (this.firstTwoSet < 2) {
this.firstTwoSet++;
this.$refs.tabasss.setScrollPosition();
}
}).catch(error => {
console.log(error);
})
},
getRightList(code) { // 加载二级科室  医院入口
this.rightArr = [];
this.rightLoading = true;
this.$ajax.post(this.$root.host, {
"MSH.1": "hospitalregistration",
"MSH.2": "listHospitalKS",
cbm: this.CBM, // 机构编码
sjksbm: code, // 科室编码
token: this.$root.loginInfo.token,
}).then(res => {
this.rightArr = [];
this.rightLoading = false;
if (typeof res.rows != 'undefined' && res.rows.length > 0) {
res.rows.forEach(item => {
this.rightArr.push({
label: item.CKSMC
});
})
}
this.list[code] = this.rightArr;
if (this.firstTwoSet < 2) {
this.firstTwoSet++;
this.$refs.tabasss.setScrollPosition();
}
}).catch(error => {
console.log(error);
})
},
getList(code) {
if (!this.list[code]) {
this.getRight(code);
} else {
this.rightArr = this.list[code];
}
},
onLiClick(i, j, obj) {
this.gotoDetailIndex = i;
this.getList(obj.label);
},
onConClick(i, j, obj) {
this.newConIndex = j;
this.$router.push({
name: 'doctorList',
params: {
ksmc: obj.label,
'ysbm': '',
'type': '1',
'ksbm': ''
}
});
},
}
}



